import React, { Component } from 'react'

export default class BlogItem extends Component {
    // begin chuyendoi duong dan URL tu tieng viet sang tieng anh k dau
    chuyenDoiURL = (str) => {
        // Chuyn ht sang ch thng
        str = str.toLowerCase();

        // xa du
        str = str.replace(/(||||||||||||||||)/g, 'a');
        str = str.replace(/(||||||||||)/g, 'e');
        str = str.replace(/(||||)/g, 'i');
        str = str.replace(/(||||||||||||||||)/g, 'o');
        str = str.replace(/(||||||||||)/g, 'u');
        str = str.replace(/(||||)/g, 'y');
        str = str.replace(/()/g, 'd');

        // Xa k t c bit
        str = str.replace(/([^0-9a-z-\s])/g, '');

        // Xa khong trng thay bng k t -
        str = str.replace(/(\s+)/g, '-');

        // xa phn d -  u
        str = str.replace(/^-+/g, '');

        // xa phn d -  cui
        str = str.replace(/-+$/g, '');

        // return
        return str;
    }
    // end chuyendoi duong dan URL tu tieng viet sang tieng anh k dau  
    render() {
        return (
            <div className="post-box">
            <div className="row">
                <div className="col-sm-5">
                    <div className="teaser">
                        
                            
                    <img src={this.props.image1} alt="" />
                    </div>
                </div>
                <div className="col-sm-7">
                    <div className="meta">
                        <div className="entry-title">
                            <a href={"/BlogYoga/" + this.chuyenDoiURL(this.props.name) + "." + this.props.hlvId}>
                                <h2>
                                    <span className="span-name-blog">
                                        {this.props.name} </span>
                                </h2>
                               
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}
