import React, { Component } from 'react'
import './odercart.css';
import {DataContext} from '../Context'
import {Link} from 'react-router-dom'
export default class odercart extends Component {
  static contextType = DataContext;

  componentDidMount(){
      this.context.getTotal();
  }
    render() {
      const {cart,increase,reduction,removeProduct,total} = this.context;
      if(cart.length === 0){
        return <h2 style={{textAlign:"center"}}>Nothings Product</h2>
    }else{
      return (
        <>
        
        <div className=" banner-productss banner-cart">
                    <div class="slider-text">
                        <div class="content-text wow bounceInUp" data-wow-delay="0.1s">
                        
                        </div>
                    </div>
                </div>
        <div className="container-fluid container-fluid-oudercart">
        <div className="row">
          <div className="col-md-6 col-xs-12 col-md-6-col-xs-12">
            <div className="step-sections " step={1}>
              <div className="section">
                <div className="section-header">
                  <h2 className="section-title">Thng tin giao hng</h2>
                </div>
                <div className="section-content section-customer-information no-mb">
                  <input name="utf8" type="hidden" defaultValue="" />
                  <div className="inventory_location_data">
                    <input name="customer_shipping_district" type="hidden" defaultValue />
                    <input name="customer_shipping_ward" type="hidden" defaultValue />
                  </div>
                  <p className="section-content-text">
                    Bn  c ti khon?
                    <a href="/account/login?urlredirect=%2Fcheckouts%2Fcd67a4d58cca462dba213f08fa0f5468%3Fstep%3D1">ng
                      nhp</a>
                  </p>
                  <div className="fieldset">
                    <div className="field field-required">
                      <div className="field-input-wrapper">
                        <label className="field-label"  />
                        <input placeholder="H v tn" autoCapitalize="off" spellCheck="false" className="field-input" size={30} type="text" id="billing_address_full_name" />
                      </div>
                    </div>
                    <div className="field field-required field-two-thirds  ">
                      <div className="field-input-wrapper">
                        <label className="field-label" />
                        <input placeholder="Email" className="field-input" size={30} type="email" id="checkout_user_email" />
                      </div>
                    </div>
                    <div className="field field-required field-third  ">
                      <div className="field-input-wrapper">
                        <label className="field-label" />
                        <input placeholder="S in thoi" autoCapitalize="off" spellCheck="false" className="field-input" size={30} maxLength={11} type="tel" id="billing_address_phone" />
                      </div>
                    </div>
                    <div className="field field-required  ">
                      <div className="field-input-wrapper">
                        <label className="field-label" htmlFor="billing_address_address1" />
                        <input placeholder="a ch" autoCapitalize="off" spellCheck="false" className="field-input" size={30} type="text" id="billing_address_address1" />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="section-content">
                  <div className="fieldset">
                    <form id="form_update_location" className="clearfix order-checkout__loading" acceptCharset="UTF-8" method="post">
                      <input name="selected_customer_shipping_province" type="hidden" defaultValue />
                      <input name="selected_customer_shipping_district" type="hidden" defaultValue />
                      <input name="selected_customer_shipping_ward" type="hidden" defaultValue />
                      <input name="utf8" type="hidden" defaultValue="" />
                      <div className="order-checkout__loading--box">
                        <div className="order-checkout__loading--circle" />
                      </div>
                      <div className="field field-show-floating-label field-required field-third ">
                        <div className="field-input-wrapper field-input-wrapper-select">
                          <label className="field-label" htmlFor="customer_shipping_province" />
                          <select className="field-input" id="customer_shipping_province" name="customer_shipping_province">
                            <option data-code="null" value="null" selected> Chn tnh / thnh
                            </option>
                            <option data-code="HC" value={50}>H Ch Minh</option>
                            <option data-code="HI" value={1}>H Ni</option>
                            <option data-code="DA" value={32}> Nng</option>
                            <option data-code="AG" value={57}>An Giang</option>
                            <option data-code="BV" value={49}>B Ra - Vng Tu</option>
                            <option data-code="BG" value={15}>Bc Giang</option>
                            <option data-code="BK" value={4}>Bc Kn</option>
                            <option data-code="BL" value={62}>Bc Liu</option>
                            <option data-code="BN" value={18}>Bc Ninh</option>
                            <option data-code="BT" value={53}>Bn Tre</option>
                            <option data-code="BD" value={35}>Bnh nh</option>
                            <option data-code="BI" value={47}>Bnh Dng</option>
                            <option data-code="BP" value={45}>Bnh Phc</option>
                            <option data-code="BU" value={39}>Bnh Thun</option>
                            <option data-code="CM" value={63}>C Mau</option>
                            <option data-code="CN" value={59}>Cn Th</option>
                            <option data-code="CB" value={3}>Cao Bng</option>
                            <option data-code="DC" value={42}>k Lk</option>
                            <option data-code="DO" value={43}>k Nng</option>
                            <option data-code="DB" value={7}>in Bin</option>
                            <option data-code="DN" value={48}>ng Nai</option>
                            <option data-code="DT" value={56}>ng Thp</option>
                            <option data-code="GL" value={41}>Gia Lai</option>
                            <option data-code="HG" value={2}>H Giang</option>
                            <option data-code="HM" value={23}>H Nam</option>
                            <option data-code="HT" value={28}>H Tnh</option>
                            <option data-code="HD" value={19}>Hi Dng</option>
                            <option data-code="HP" value={20}>Hi Phng</option>
                            <option data-code="HU" value={60}>Hu Giang</option>
                            <option data-code="HO" value={11}>Ha Bnh</option>
                            <option data-code="HY" value={21}>Hng Yn</option>
                            <option data-code="KH" value={37}>Khnh Ha</option>
                            <option data-code="KG" value={58}>Kin Giang</option>
                            <option data-code="KT" value={40}>Kon Tum</option>
                            <option data-code="LI" value={8}>Lai Chu</option>
                            <option data-code="LD" value={44}>Lm ng</option>
                            <option data-code="LS" value={13}>Lng Sn</option>
                            <option data-code="LO" value={6}>Lo Cai</option>
                            <option data-code="LA" value={51}>Long An</option>
                            <option data-code="ND" value={24}>Nam nh</option>
                            <option data-code="NA" value={27}>Ngh An</option>
                            <option data-code="NB" value={25}>Ninh Bnh</option>
                            <option data-code="NT" value={38}>Ninh Thun</option>
                            <option data-code="PT" value={16}>Ph Th</option>
                            <option data-code="PY" value={36}>Ph Yn</option>
                            <option data-code="QB" value={29}>Qung Bnh</option>
                            <option data-code="QM" value={33}>Qung Nam</option>
                            <option data-code="QG" value={34}>Qung Ngi</option>
                            <option data-code="QN" value={14}>Qung Ninh</option>
                            <option data-code="QT" value={30}>Qung Tr</option>
                            <option data-code="ST" value={61}>Sc Trng</option>
                            <option data-code="SL" value={9}>Sn La</option>
                            <option data-code="TN" value={46}>Ty Ninh</option>
                            <option data-code="TB" value={22}>Thi Bnh</option>
                            <option data-code="TY" value={12}>Thi Nguyn</option>
                            <option data-code="TH" value={26}>Thanh Ha</option>
                            <option data-code="TT" value={31}>Tha Thin Hu</option>
                            <option data-code="TG" value={52}>Tin Giang</option>
                            <option data-code="TV" value={54}>Tr Vinh</option>
                            <option data-code="TQ" value={5}>Tuyn Quang</option>
                            <option data-code="VL" value={55}>Vnh Long</option>
                            <option data-code="VT" value={17}>Vnh Phc</option>
                            <option data-code="YB" value={10}>Yn Bi</option>
                          </select>
                        </div>
                      </div>
                      <div className="field field-show-floating-label field-required field-third active">
                        <div className="field-input-wrapper field-input-wrapper-select">
                          <label className="field-label" htmlFor="customer_shipping_district" />
                          <select className="field-input" id="customer_shipping_district" name="customer_shipping_district">
                            <option data-code="null" value="null" selected>Chn qun / huyn
                            </option>
                          </select>
                        </div>
                      </div>
                      <div className="field field-show-floating-label field-required  field-third  active">
                        <div className="field-input-wrapper field-input-wrapper-select">
                          <label className="field-label" htmlFor="customer_shipping_ward" />
                          <select className="field-input" id="customer_shipping_ward" name="customer_shipping_ward">
                            <option data-code="null" value="null" selected>Chn phng / x
                            </option>
                          </select>
                        </div>
                      </div>
                    </form>
                  </div>
                  <div className="section-content section-customer-information fieldset" id="div_country_not_vietnam" style={{display: 'none'}}>
                    <div className="field field-two-thirds">
                      <div className="field-input-wrapper">
                        <label className="field-label" htmlFor="billing_address_city">Thnh ph</label>
                        <input placeholder="Thnh ph" autoCapitalize="off" spellCheck="false" className="field-input" size={30} type="text" id="billing_address_city" name="billing_address[city]" defaultValue />
                      </div>
                    </div>
                    <div className="field field-third">
                      <div className="field-input-wrapper">
                        <label className="field-label" htmlFor="billing_address_zip">M bu chnh</label>
                        <input placeholder="M bu chnh" autoCapitalize="off" spellCheck="false" className="field-input" size={30} type="text" id="billing_address_zip" name="billing_address[zip]" defaultValue />
                      </div>
                    </div>
                  </div>
                </div>
                <div id="change_pick_location_or_shipping">
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-6 col-xs-12 colll">
            
            {
                        cart.map(item =>(
                            <div className="details cart" key={item.id}>
                                <img className="image-odercart" src={item.image} alt=""/>
                                <div className="box">
                                    <div className="row">
                                        <h3 className="name-cart name-cart-odercart">{item.name}</h3>
                                        <span className="price-oudercart">${item.price * item.count}</span>
                                    </div>
                                    {/* <Colors colors={item.colors}/> */}
                                    <p>Khi lng: {item.size}g</p>
                                    {/* <p>{item.content}</p> */}
                                
                                </div>
                               
                            </div>
                        ))
                    }
                    <div className="total">
                        <Link to="/odercart">Thanh ton</Link>
                        <h3 className="price-oudercart">Tng tin: ${total}</h3>
                    </div>
          </div> 
        </div>
      </div>
    </>
    
    )
    }
       
    }
}
