import React, { Component } from 'react'

export default class ProductPtItem extends Component {
      // begin chuyendoi duong dan URL tu tieng viet sang tieng anh k dau
  chuyenDoiURL = (str) => {
    // Chuyn ht sang ch thng
    str = str.toLowerCase();

    // xa du
    str = str.replace(/(||||||||||||||||)/g, 'a');
    str = str.replace(/(||||||||||)/g, 'e');
    str = str.replace(/(||||)/g, 'i');
    str = str.replace(/(||||||||||||||||)/g, 'o');
    str = str.replace(/(||||||||||)/g, 'u');
    str = str.replace(/(||||)/g, 'y');
    str = str.replace(/()/g, 'd');

    // Xa k t c bit
    str = str.replace(/([^0-9a-z-\s])/g, '');

    // Xa khong trng thay bng k t -
    str = str.replace(/(\s+)/g, '-');

    // xa phn d -  u
    str = str.replace(/^-+/g, '');

    // xa phn d -  cui
    str = str.replace(/-+$/g, '');

    // return
    return str;
  }
// end chuyendoi duong dan URL tu tieng viet sang tieng anh k dau  
    render() {
        return (
            <div className="col-md-2 col-sm-6 col-xs-12">
                <div className="img-col">
                    <img className="wow bounceInDown" src={this.props.image} alt="" />
                </div>
                <div className="content-name ">
                    <div className="name">
                        {this.props.name}
                    </div>
                   <p>HLV C Nhn</p>
                </div>
                <div className="button-click">
                <a href={"/chitiet/" + this.chuyenDoiURL(this.props.name)  + "." + this.props.hlvId} className="btn btn-primary">Xem Thm</a>
                </div>
            </div>
        )
    }
}
